<template>
  <div style="width: 100%;height: 100vh;color:var(--color-neutral-10);margin: 0 auto">
    <div style="padding: 40px 0 0 0;margin: 0 auto;max-width: 1550px;width: 90%;">
      <p style="font-size: 24px;font-weight: bold;margin-bottom: 20px">学校信息管理</p>
    </div>
    <div style="width: 100%;height: calc(100% - 96px);position: relative;overflow-y: auto;">
      <div style="width: 90%;height: 100%;margin: 0 auto;max-width: 1550px;">
        <div style="margin-bottom: 20px;width: 100%;position: relative">
          <a-input :disabled="data.loading" v-model="data.form.universityName" style="border: 0px;line-height: 36px;display: inline-block;width: 200px;margin-right: 10px;background-color: transparent;border-bottom: 1px solid var(--color-neutral-10)" placeholder="学校名称"></a-input>
          <v-btn :loading="data.loading" style="margin-right: 10px" variant="text"
                 @click="data.form.page=1;getTable()"
          >搜索/刷新</v-btn>
          <a-trigger trigger="click" :unmount-on-close="false">
            <v-btn :disabled="data.loading" style="margin-right: 10px" variant="text"

            >导入</v-btn>
            <template #content>
              <div style="padding: 20px 20px;border-radius: 10px;background-color: var(--color-neutral-1);box-shadow: 0 4px 10px rgba(0,0,0,0.1);max-width: 300px">
                <p style="color: var(--color-neutral-10)">选择的表格文件的第一张工作表应包含导入数据，并且工作表从A1单元格到AN1单元格的内容应依次为：学校名称、省份、城市、等级、是否为985、是否为211、是否为双一流、是否为强基、区名、类别、性质、建校日期、面积、旧名、简 称、主管部门、国家重点学科、一级硕士点数量、二级硕士点数量、一级博士点数量、二级博士点数量、校友会排名、US世界排名、科研项目数量、软科排名、邮箱、地址、学校网址、电话、简介、双一流学科、专业数量、国家特色专业、各省最低分、宿舍介绍、食堂介绍、毕业生去向落实率、毕业生签约地区流向、毕业生签约单位性质、主要签约单位。</p>
                <a-upload @success="imported" :action="appData.serverURL + '/bg/school/import'"></a-upload>
              </div>
            </template>
          </a-trigger>
          <v-btn :disabled="data.loading || data.form.total==0" style="margin-right: 0px" variant="text"
                 @click="deleteAll"
          >清空</v-btn>
          <div v-if="data.form.total>data.form.size" style="display: inline-block;float: right">
            <v-btn variant="text" @click="data.form.page=1;getTable()" :disabled="data.loading || data.form.page==1" style="margin-left: 5px">第一页</v-btn>
            <v-btn variant="text" @click="data.form.page-=1;getTable()" :disabled="data.loading || data.form.page==1" style="margin-left: 5px">上一页</v-btn>
            <a-trigger trigger="click" :unmount-on-close="false">
              <v-btn variant="text" :disabled="data.loading" style="margin-left: 5px">{{ data.form.page.toString() + '/' + data.form.pageTotal.toString() }}</v-btn>
              <template #content>
                <div style="padding: 20px 20px;border-radius: 10px;background-color: var(--color-neutral-1);box-shadow: 0 4px 10px rgba(0,0,0,0.1)">
                  <a-input v-model="data.form.pageInput" placeholder="页码" style="width: 70px;"></a-input><a-button :loading="data.loading" @click="tableChangePage" :disabled="data.form.pageInput==''" style="margin-left: 5px">转到</a-button>
                  <br>
                  <p v-if="data.form.warningText!=''" style="color: red">{{ data.form.warningText }}</p>
                </div>
              </template>
            </a-trigger>
            <v-btn variant="text" @click="data.form.page+=1;getTable()" :disabled="data.loading || data.form.page==data.form.pageTotal" style="margin-left: 5px">下一页</v-btn>
            <v-btn variant="text" @click="data.form.page=data.form.pageTotal;getTable()" :disabled="data.loading || data.form.page==data.form.pageTotal" style="margin-left: 5px">最后一页</v-btn>
          </div>
        </div>
        <a-table @row-click="tableRowClick" :pagination="false" :loading="data.loading" style="width: 100%;margin-bottom: 40px;" :columns="[
  { dataIndex: 'mark', title: '标记' },
  { dataIndex: 'universityName', title: '学校名称' },
  { dataIndex: 'p', title: '省份' },
  { dataIndex: 'city', title: '城市' },
  { dataIndex: 'town_name', title: '区名' },
  { dataIndex: 'level', title: '等级' },
  { dataIndex: 'is985', title: '是否为985' },
  { dataIndex: 'is211', title: '是否为211' },
  { dataIndex: 'isDualClass', title: '是否为双一流' },
  { dataIndex: 'isQJ', title: '是否为强基' },
  { dataIndex: 'type_name', title: '类别' },
  { dataIndex: 'school_nature_name', title: '性质' },
  { dataIndex: 'create_date', title: '建校日期' },
  { dataIndex: 'belong', title: '主管部门' },
]" :data="data.table" />
      </div>
    </div>
  </div>
  <a-modal v-model:visible="data.showEditSchoolInfoForm" @ok="changeSchoolInfo" :closable="data.isSaving==false" :mask-closable="data.isSaving==false" :ok-loading="data.isSaving">
    <template #title>
      编辑学校信息
    </template>
    <div style="max-height: calc(80vh - 48px - 65px);overflow-y: scroll">
      <data-editor :read-only="data.isSaving==true" @dataChanged="schoolInfoChanged" style="color: var(--color-neutral-10);width: 100%;background-color: white;border-radius: 5px;padding: 15px 15px 5px 15px" v-if="data.showEditSchoolInfoForm" :data="data.editSchoolInfoSelectedItem"></data-editor>
    </div>
  </a-modal>
</template>

<script>
import tool from '@/utlis/tool'
import config from '@/config'
import dataEditor from "@/components/dataEditor.vue";
import axios from "axios";

export default {
  components:{
    dataEditor
  },
  name: '',
  data() {
    return {
      data:{
        loading:false,
        form:{
          page:1,
          size:25,
          total:0,
          pageTotal:0,
          pageInput:'',
          warningText:'',
          universityName:''
        },
        table:[],
        showEditSchoolInfoForm:false,
        editSchoolInfoSelectedItem:undefined,
        editSchoolInfoSelectedItemMark:undefined,
        isSaving:false,
      }
    }
  },

  methods: {
    async changeSchoolInfo(){
      this.data.isSaving = true
      this.data.editSchoolInfoSelectedItem['mark'] = this.data.editSchoolInfoSelectedItemMark.toString()
      const data = tool.encodeString(JSON.stringify({
        token:this.bgData.loginedAdminInfo.token,
        data:this.data.editSchoolInfoSelectedItem
      }))
      const file = new Blob([data], { type: 'text/plain' });
      const formData = new FormData();
      formData.append('file', file, 'filename.txt');
      await axios.post(config.serverURL + '/bg/school/change', formData, {
        headers: {
          'Content-Type': 'multipart/form-data'
        }
      }).then(response => {
        console.log(response)
      })
      this.data.isSaving = false
      this.getTable()
    },
    schoolInfoChanged(data){
      this.data.editSchoolInfoSelectedItem = JSON.parse(JSON.stringify(data)).data
    },
    tableRowClick(data){
      for(let i=0;i<Object.keys(data).length;i++){
        try{
          data[Object.keys(data)[i]] = JSON.parse(data[Object.keys(data)[i]])
        }catch (e){

        }
      }
      data = JSON.parse(JSON.stringify(data))
      this.data.editSchoolInfoSelectedItemMark = data.mark
      delete data['mark']
      this.data.editSchoolInfoSelectedItem = data
      this.data.showEditSchoolInfoForm = true
    },
    async deleteAll(){
      this.data.loading = true
      await tool.axiosPostWithCheckStatus(config.serverURL,'/bg/school/delete',{
        token:this.bgData.loginedAdminInfo.token
      })
      this.data.loading = false
      this.data.form.page = 1
      this.getTable()
    },
    tableChangePage(){
      this.data.form.warningText=''
      let a = parseInt(this.data.form.pageInput)
      if(isNaN(a)){
        this.data.form.warningText='页码应为数字'
        return
      }else{
        if(a<1 || a>this.data.form.pageTotal){
          this.data.form.warningText='页码应在1到' + this.data.form.pageTotal.toString() + '之间'
          return
        }
        this.data.form.page = a
        this.getTable()
      }
    },
    imported(){
      this.getTable()
    },
    async getTable(){
      this.data.loading = true
      let re = (await tool.axiosPostWithCheckStatus(config.serverURL,'/bg/school/list',{
        token:this.bgData.loginedAdminInfo.token,
        page:this.data.form.page,
        size:this.data.form.size,
        universityName:this.data.form.universityName
      }))
      this.data.form.total = re.results.total
      this.data.form.pageTotal = tool.getPageTotal(re.results.total,this.data.form.size)
      this.data.table = re.results.data
      this.data.loading = false
    }
  },

  //生命周期 - 挂载完成（可以访问DOM元素）",html模板已渲染
  mounted() {
    this.getTable()
  },

  props: {
    appData:undefined,
    bgData:undefined
  },

  //监听属性 类似于data概念",
  computed: {},

  //监控data中的数据变化",
  watch: {},

  //生命周期 - 创建完成（可以访问当前this实例）",数据模型已加载，方法已加载,html模板已加载,html模板未渲染
  created() {
  },

  //生命周期 - 挂载之前",html模板未渲染
  beforeMount() {
  },

  //生命周期 - 更新之前",数据模型已更新,html模板未更新
  beforeUpdate() {
  },

  // 生命周期 - 销毁之前调用
  beforeDestroy() {
  },

  //生命周期 - 更新之后",数据模型已更新,html模板已更新
  updated() {
  },

  //生命周期 - 销毁完成"
  destroyed() {
  },

  //如果页面有keep-alive缓存功能，这个函数会触发",
  activated() {
  }
}
</script>

<style scoped>

</style>